<template>
  <div style="z-index: 3; position: relative">
    <canvas id="planet-chart" ref="goldChart"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js";

export default {
  name: "GoldChart",
  mounted() {
    const ctx = this.$refs.goldChart.getContext("2d");

    // Create your custom linear gradient
    const gradient = ctx.createLinearGradient(0, 0, ctx.canvas.width, 0);
    gradient.addColorStop(0.0, "#3AAA82");
    gradient.addColorStop(0.353, "#42B28A");
    gradient.addColorStop(0.598, "#4EBEAA");
    gradient.addColorStop(1.0, "#3EC6D9");

    // Create the chart
    new Chart(ctx, {
      type: "line",
      data: {
        labels: [
          "Mon",
          "Tue",
          "Wed",
          "Thu",
          "Fri",
          "Mon",
          "Tue",
          "Wed",
          "Thu",
          "Fri",
        ],
        datasets: [
          {
            label: "DJT/USD",
            data: [2.24, 2.28, 2.19, 2.65, 2.32, 2.21, 2.34, 2.63, 2.42, 2.18],
            borderColor: gradient, // Apply gradient here
            backgroundColor: "transparent",
            borderWidth: 2,
            pointRadius: 2.2,
            lineTension: 0.4, // Use `lineTension` in v2.x
          },
        ],
      },
      options: {
        tooltips: {
          // Disable the on-canvas tooltip
          enabled: true,
        },
        layout: {
          padding: {
            left: 24,
            right: 24,
            top: 24,
            bottom: 24,
          },
        },
        responsive: true,
        legend: { display: false },
        scales: {
          xAxes: [{ display: false }],
          yAxes: [{ display: false }],
        },
        animation: { duration: 1000 },
      },
    });
  },
};
</script>
