@include main.mobile-device {
  #planet-chart {
    display: block;
    height: 150px;
    width: 400px;
    z-index: 3;
  }
}
@media only screen and (max-width: 300px) {
  #planet-chart {
    display: block;
    height: 175px;
    width: 350px;
    z-index: 3;
  }
}
</style>]
