<template>
  <div style="z-index: 3; position: relative">
    <canvas id="planet-chart" ref="goldChart"></canvas>
  </div>
</template>

<script>
import Chart from "chart.js";

export default {
  name: "GoldCharttwo",
  mounted() {
    const ctx = this.$refs.goldChart.getContext("2d");

    const gradientz = ctx.createLinearGradient(0, 0, 0, ctx.canvas.height);
    gradientz.addColorStop(0, "rgba(174, 148, 255, 0.5)"); // Top (starting color)
    gradientz.addColorStop(1, "rgba(0, 0, 0, 0)"); // Bottom (transparent)

    // Create your custom linear gradient
    const gradient = ctx.createLinearGradient(0, 0, ctx.canvas.width, 0);
    gradient.addColorStop(0.0, "#6a43ff");
    gradient.addColorStop(0.353, "#596ff5");
    gradient.addColorStop(0.598, "#957cf4");
    gradient.addColorStop(1.0, "#bfaaff");

    // Create the chart
    new Chart(ctx, {
      type: "line",
      data: {
        labels: [
          "Mon",
          "Tue",
          "Wed",
          "Thu",
          "Fri",
          "Mon",
          "Tue",
          "Wed",
          "Thu",
          "Fri",
        ],
        datasets: [
          {
            label: "DJT/USD",
            data: [2.24, 2.28, 2.69, 2.65, 2.82, 2.21, 2.34, 2.53, 2.22, 2.18],
            borderColor: gradient, // Apply gradient here
            backgroundColor: "transparent",
            borderWidth: 2,
            pointRadius: 2.2,
            lineTension: 0.4, // Use `lineTension` in v2.x
          },
        ],
      },
      options: {
        tooltips: {
          // Disable the on-canvas tooltip
          enabled: true,
        },
        layout: {
          padding: {
            left: 24,
            right: 24,
            top: 24,
            bottom: 24,
          },
        },
        responsive: true,
        legend: { display: false },
        scales: {
          xAxes: [{ display: false }],
          yAxes: [{ display: false }],
        },
        animation: { duration: 1000 },
      },
    });
  },
};
</script>
